import React, { useEffect, useState } from "react";

const Cell = (props) => {
    const [initialVal] = [props.initialVal];
    const [val, setVal] = useState(initialVal);

    useEffect(() => {
        setVal(initialVal);
    }, [initialVal]);

    // need onChange if you're setting a value attribute. otherwise u can't change the input field
    return <input type="text" className="cell" maxLength="1" value={val} onChange={(evt) => setVal(evt.target.value)}></input>;
}

export default Cell;