import React, { useState } from 'react';
import Cell from './Cell';
import solveSudoku from '../logic/solveSudoku';

const getInitialBoard = () => {
    let initialBoard = [[], [], [], [], [], [], [], [], []];

    for (let row = 0; row < 9; row++) {
        for (let col = 0; col < 9; col++) {
            initialBoard[row][col] = 0;
        }
    }

    return initialBoard;
}

const showBoard = (board, setBoard) => {
    let actualBoard = [[], [], [], [], [], [], [], [], []]

    for (let row = 0; row < 9; row++) {
        for (let col = 0; col < 9; col++) {
            actualBoard[row][col] = <Cell key={row.toString() + ", " + col.toString()} initialVal={board[row][col]} />
        }
    }

    for (let row = 0; row < 9; row++) {
        actualBoard[row] = <div key={row}>{actualBoard[row]}</div>
    }

    return actualBoard;
}

const handleSubmit = async (evt, setBoard) => {
    evt.preventDefault();

    let boardToSolve = [[], [], [], [], [], [], [], [], []];

    for (let i = 0; i < 81; i++) {
        let rowNum = Math.floor(i / 9);
        boardToSolve[rowNum][i - 9 * rowNum] = evt.target[i].value;
    }

    let solvedBoard = await solveSudoku(boardToSolve, setBoard);
    setBoard(solvedBoard);
}

const Board = () => {
    const [board, setBoard] = useState(getInitialBoard());

    return (
        <form onSubmit={(evt) => handleSubmit(evt, setBoard)}>
            <div>{board != null && showBoard(board, setBoard)}</div>
            <button type="submit" id="submitButton">Submit</button>
        </form >
    );
}

export default Board;